import React from "react";
import Modal from "react-modal";
import { TextArea } from "semantic-ui-react";
import SearchCover from "./SearchCover";
import CoverList from './CoverList';
import QuoteList from "./QuoteList";
import stylesheet from '../css/BookDescription.css';
import image from "../img/interrogation.png";


class BookDescription extends React.Component{

    constructor(props){
        super();

        
    }

    state = {
        modalSureHandler: false,
        modalEditHandler: false,
        modalImgEditHandler: false,
        modalDescriptionHandler: false,
        urlModalHandler: false,
        bookNome: [],
        bookAutor: [],
        bookTags: [],
        bookImage: [],
        bookDescription: [],
        image: []
    }

    changeModalStateSure = () => {
        this.setState({modalSureHandler: !this.state.modalSureHandler});
    }

    changeModalStateEdit = () => {
        if(this.state.modalEditHandler){
            this.setState({
                            bookNome: [],
                            bookAutor: [],
                            bookTags: [],
                            bookImage: [],
                            bookDescription: []
                        });
        }else{
            this.setState({
                bookNome: this.props.selectedBook.entry[0],
                bookAutor: this.props.selectedBook.entry[1],
                bookTags: this.props.selectedBook.entry[2],
                bookImage: this.props.selectedBook.entry[3],
                bookDescription: this.props.selectedBook.entry[5]
            });
        }

        this.setState({modalEditHandler: !this.state.modalEditHandler});
    }

    changeModalStateImgEdit = () => {
        this.setState({modalImgEditHandler: !this.state.modalImgEditHandler});
    }

    changeModalStateDescription = () => {
        this.setState({modalDescriptionHandler: !this.state.modalDescriptionHandler});
    }

    changeURLModalState = () => {
        this.setState({urlModalHandler: !this.state.urlModalHandler});
    }

    useUrl = () => {
        // console.log(this.state.image)
        this.changeURLModalState();
        this.props.showingGallery(false);
            
    }

    returnForRemoval = () =>{
        this.changeModalStateSure();
        this.props.remBook(this.props.selectedBook.entry[6]);
    }

    changeImage = () => {
        this.setState({bookImage: this.state.image});
        this.changeModalStateImgEdit();
    }

    returnDataEdit = () => {
        let tags = [];
        if(this.state.bookTags.includes(",")){
            
            tags = this.state.bookTags.split(",");
            if(tags[tags.length-1] === "")
                tags.splice(tags.length-1, 1);
        }else
            tags = this.state.bookTags;

        this.props.editBook([this.state.bookNome, this.state.bookAutor, tags, this.state.bookImage, this.props.selectedBook.entry[4], this.state.bookDescription, this.props.selectedBook.entry[6], this.props.selectedBook.entry[7]]);
        this.changeModalStateEdit();
    }

    onSelectImageReturn = (selected) => {
        this.setState({image: selected[0].slice(0,2), id: selected[0][2]});
        this.props.showingGallery(selected[1]);

        if(selected[0].length === 0){
            this.setState({nome: []});
            this.setState({autor: []});
        }else{
            if((this.state.nome === undefined) || (!this.state.nome.length))
            this.setState({nome: selected[0][3]});

            if((this.state.autor === undefined) || (!this.state.autor.length))
                this.setState({autor: selected[0][4]});

            if((this.state.description === undefined) || (!this.state.description.length))
                this.setState({description: selected[0][5]});
        }  
    }

    render(){
        // console.log(this.props.selectedBook)
        if(this.props.selectedBook.length === 0)
            return(
                <div id="emptySubscription">
                    <img src={image} alt="Um ponto de interrogao com um crculo em volta" style={{maxWidth: "30%"}}/>
                    <p style={{fontSize: "30px", textAlign:"center"}}>Clique no boto '+' na barra de cones para adicionar um livro  sua coleo!</p>
                </div>
            )
        else{
            const tags = this.props.selectedBook.entry[2].map((tag, i) =>   <div>
                                                                                <a class="ui tag label">{tag}</a>
                                                                            </div>
                                                            );
            return(
                <div className="flex" style={{height: "100%", flexDirection:"column"}}>
                    <div id="bookHeader" className="ui flex" style={{height: "40%"}}>
                        <div style={{height: "100%", width:"30%"}}>
                            <img src={this.props.selectedBook.entry[3][0]} alt={"nada ainda"} style={{maxHeight: "100%", maxWidth: "95%"}} />
                        </div>
                        <div style={{height: "100%", width:"70%", display:"flex", flexDirection: "column"}}>
                            <div id="headerText" style={{height: "100%", width: "100%", display:"flex", flexDirection: "row"}}>
                                <div style={{width: "85%"}}>
                                    <h1><b>{this.props.selectedBook.entry[0]}</b></h1>
                                    <h3><i>{this.props.selectedBook.entry[1]}</i></h3>
                                    {this.props.selectedBook.entry[5].length === 0 ? <p>Nenhuma descrio disponvel</p> : this.props.selectedBook.entry[5] > 90 ? <p onClick={this.changeModalStateDescription}>{this.props.selectedBook.entry[5]}</p> : <p onClick={this.changeModalStateDescription}>{this.props.selectedBook.entry[5].substr(0, 90)}...</p> }
                                </div>
                                <div style={{width: "15%"}}>
                                    <div className='ui icon button' style={{backgroundColor: "transparent"}} onClick={this.changeModalStateSure} >
                                        <i className="trash icon big"></i>
                                    </div>  
                                    <div className='ui icon button' style={{backgroundColor: "transparent"}} onClick={this.changeModalStateEdit}>
                                        <i className="edit icon big"></i>
                                    </div>
                                </div>
                            </div>
                            <div style={{width:"100%", display: "flex", justifyContent: "center"}}>
                                <div style={{overflowX: "scroll", padding:"1%", borderRadius: "15px"}}>
                                    <div style={{display: "flex"}}>
                                        {tags}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div id="quoteList" style={{height: "45%"}}>
                        <QuoteList quotes={this.props.selectedBook.entry[4]} addQuote={this.props.addQuote} remQuote={this.props.remQuote} editQuote={this.props.editQuote} bookInfo={this.props.selectedBook} />
                    </div>
                    <Modal id="areUsureModal" isOpen={this.state.modalSureHandler} onRequestClose={this.changeModalStateSure} style={{
                        overlay: {
                            color: "lightgrey"
                        },
                        content: {
                            color: 'black',
                            height: "20%",
                            top: "25%",
                            width: "30%",
                            left: "35%"
                        }}}
                    >
                        <div style={{height: "100%", display: "flex", flexDirection: "column",justifyContent: "center"}}>
                            <p>Tem certeza que gostaria de deletar o livro?</p>
                            <div>
                                <button className="ui primary button" onClick={this.returnForRemoval}>Deletar</button>    <button className="ui button" onClick={this.changeModalStateSure}>Cancelar</button>
                            </div>
                        </div>
                    </Modal>
                    <Modal id="descriptionModal" isOpen={this.state.modalDescriptionHandler} onRequestClose={this.changeModalStateDescription} style={{
                        overlay: {
                            color: "lightgrey"
                        },
                        content: {
                            color: 'black',
                            
                            // height: "20%",
                            // top: "25%",
                            // width: "30%",
                            left: "35%",
                            right: "35%"
                        }}}
                    >
                        <h4 className="ui horizontal divider header">
                            <b>Description</b>
                        </h4>
                        <div style={{height: "90%", display: "flex", flexDirection: "column", justifyContent:"space-between"}}>
                            <p style={{textAlign: "justify"}}>{this.props.selectedBook.entry[5]}</p>
                            <div>
                                <button className="ui button" onClick={this.changeModalStateDescription}>Fechar</button>
                            </div>
                        </div>
                    </Modal>
                    <Modal id="bookEditModal" isOpen={this.state.modalEditHandler} onRequestClose={this.changeModalStateEdit} style={{
                        overlay: {
                            color: "lightgrey"
                        },
                        content: {
                            color: 'black',
                            height: "60%",
                            top: "20%",
                            width: "70%",
                            left: "15%"
                        }}}
                    >
                        <h4 className="ui horizontal divider header">
                            <b>{this.state.bookNome}</b>
                        </h4>
                        <div style={{width:"100%", height:"80%", display:"flex", justifyContent:"space-around"}}>
                            <div style={{height: "100%", width: "30%"}}>
                                <img id="imageEdit" src={this.state.bookImage[1]} alt={"nada ainda"} style={{height: "100%"}} onClick={this.changeModalStateImgEdit}/>
                            </div>
                            <div style={{width:"60%", display:"flex", flexDirection:"column", justifyContent: "space-around"}}>
                                <form className="ui form" style={{width:"100%", display:"flex", flexDirection:"row",  justifyContent: "space-between"}}>
                                    <div className="ui small labeled input">
                                        <div className="ui label">
                                            Nome:
                                        </div>
                                        <input type="text" style={{width: "200px"}} value={this.state.bookNome} onChange={(event) => this.setState({bookNome: event.target.value})}/>
                                    </div>
                                    <div className="ui small labeled input" style={{marginLeft: "20px"}}>
                                        <div className="ui label">
                                            Autor: 
                                        </div>
                                        <input type="text" style={{width: "200px"}} value={this.state.bookNome} onChange={(event) => this.setState({bookNome: event.target.value})}/>
                                    </div>
                                </form>
                                <form className="ui form" style={{marginTop: "5%"}}>
                                    <div className="ui small fluid labeled input">
                                        <div className="ui label">
                                            Tags:
                                        </div>
                                        <input type="text" value={this.state.bookTags} onChange={(event) => this.setState({bookTags: event.target.value})}/>
                                    </div>
                                </form>
                                <form className="ui fluid form" style={{marginTop: "5%"}}>
                                    <div className="ui label">
                                        Description:
                                    </div>
                                    <TextArea value={this.state.bookDescription} onChange={(event) => this.setState({bookDescription: event.target.value})}/>
                                </form>
                            </div>
                        </div>
                        <div style={{position:"absolute", right:"1%", bottom: "5%", display:"flex", flexDirection: "row-reverse"}}>
                            <button className="ui primary button" onClick={this.returnDataEdit}>Salvar</button>    <button className="ui button" onClick={this.changeModalStateEdit}>Cancelar</button>
                        </div>
                    </Modal>
                    <Modal id="imageEditModal" isOpen={this.state.modalImgEditHandler} onRequestClose={this.changeModalStateImgEdit} style={{
                        overlay: {
                            color: "lightgrey"
                        },
                        content: {
                            color: 'black',
                            height: "70%",
                            top: "15%",
                            width: "70%",
                            left: "15%"
                        }}}
                    >
                        <div style={{height:"70%"}}>
                            <div style={{width:"100%", display: "flex", flexDirection: "column", alignItems: "center", marginBottom: "3%"}}>
                                <SearchCover onSearchSubmit={this.props.onCoverSearchSubmit}/>
                                <a style={{cursor: "pointer"}} onClick={this.changeURLModalState}>Carregar por URL</a>
                            </div>
                            <div style={{width:"100%", height:"80%", display:"flex", justifyContent:"space-around"}}>
                                <div style={{height:"100%",width:"30%", marginRight:"2%"}}>
                                    <img id="imageEdit" src={this.state.bookImage[0]} alt={"nada ainda"} style={{float:"right", border: "1px solid gray"}}/>
                                </div>
                                <div style={{height:"100%", width:"80%"}}>
                                    <CoverList images={this.props.images} showGallery={this.props.showGallery} onSelectReturn={this.onSelectImageReturn} selectedImage={this.state.image}/>
                                </div>
                                <div style={{position: "absolute", right: "5%", bottom: "5%"}}>
                                    <button className="ui primary button" onClick={this.changeImage}>Salvar</button>    <button className="ui button" onClick={this.changeModalStateImgEdit}>Cancelar</button>
                                </div>
                            </div>
                        </div>
                    </Modal>
                    <Modal id="urlModal" isOpen={this.state.urlModalHandler} onRequestClose={this.changeURLModalState} style={{
                        overlay: {
                            color: "lightgrey"
                        },
                        content: {
                            color: 'lightgrey',
                            height: "20%",
                            top: "25%",
                            width: "30%",
                            left: "35%"
                        }}}
                    >
                        <form className='ui form'>
                            <input type="text" placeholder="Digite o url da imagem" onChange={(event) => this.setState({image: [event.target.value, event.target.value]})}></input>
                        </form>
                        <div className="ui" style={{marginTop:"2%"}}>
                            <button className="ui primary button" onClick={this.useUrl}>Usar</button> <button className="ui button" onClick={this.changeURLModalState}>Cancelar</button>
                        </div>
                        
                    </Modal>
                </div>
            )
        }
            
    }


}

export default BookDescription;